.Preloader_water__3VFjW{
    width:400px;
    height: 400px;
    background-color: skyblue;
    border-radius: 50%;
    position: relative;
    box-shadow: inset 0 0 30px 0 rgba(0,0,0,.5), 0 4px 10px 0 rgba(0,0,0,.5);
    overflow: hidden;
}
.Preloader_water__3VFjW:before, .Preloader_water__3VFjW:after{
    content:'';
    position: absolute;
    width:400px;
    height: 400px;
    top:-150px;
    background-color: #fff;
}
.Preloader_water__3VFjW:before{
    border-radius: 45%;
    background:rgba(255,255,255,.7);
    -webkit-animation:Preloader_wave__2Z1pz 5s linear infinite;
            animation:Preloader_wave__2Z1pz 5s linear infinite;
}
.Preloader_water__3VFjW:after{
    border-radius: 35%;
    background:rgba(255,255,255,.3);
    -webkit-animation:Preloader_wave__2Z1pz 5s linear infinite;
            animation:Preloader_wave__2Z1pz 5s linear infinite;
}
@-webkit-keyframes Preloader_wave__2Z1pz{
    0%{
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes Preloader_wave__2Z1pz{
    0%{
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

